
.a-list{width:100%;display:-webkit-box;display:-ms-flexbox;display:-webkit-flex;display:flex;-webkit-box-pack:start;-ms-flex-pack:start;-webkit-justify-content:flex-start;justify-content:flex-start;-webkit-box-align:start;-ms-flex-align:start;-webkit-align-items:flex-start;align-items:flex-start;-ms-flex-wrap:wrap;-webkit-flex-wrap:wrap;flex-wrap:wrap;margin-bottom:50px
}
.a-list article{width:30%;height:auto;margin-left:1%;margin-bottom:20px;padding:1%;-webkit-box-shadow:0 15px 45px rgba(0,0,0,0.15);box-shadow:0 15px 45px rgba(0,0,0,0.15);-webkit-animation:showA 1s ease-out 1;animation:showA 1s ease-out 1
}
@-webkit-keyframes showA{
0%{opacity:0;-webkit-transform:translate3d(0, 50px, 0);transform:translate3d(0, 50px, 0)
}
100%{opacity:1;-webkit-transform:translate3d(0, 0, 0);transform:translate3d(0, 0, 0)
}
}
@keyframes showA{
0%{opacity:0;-webkit-transform:translate3d(0, 50px, 0);transform:translate3d(0, 50px, 0)
}
100%{opacity:1;-webkit-transform:translate3d(0, 0, 0);transform:translate3d(0, 0, 0)
}
}
.a-list article .article a.photo{height:0;width:100%;display:block;overflow:hidden;position:relative;margin-bottom:10px;padding-bottom:55.71%;cursor:pointer
}
.a-list article .article a.photo span{display:block;position:absolute;width:100%;height:100%;opacity:1;background-size:cover;background-position:center;-webkit-transition:opacity 0.3s ease-in;transition:opacity 0.3s ease-in
}
.a-list article .article .article-meta{height:23px;line-height:23px;overflow:hidden
}
.a-list article .article .article-meta p{float:left;color:#bbb
}
.a-list article .article .article-meta a{text-decoration:none;color:#62bbc3
}
.a-list article .article .article-meta .article-link{margin-right:6px
}
.a-list article .article .article-title{margin-bottom:10px;font-size:22px;font-weight:400;line-height:1.5em;word-wrap:break-word;overflow:hidden;height:33px
}
.a-list article .article .article-title a{text-decoration:none;color:#444;-webkit-transition:color 0.3s ease-in;transition:color 0.3s ease-in
}
.a-list article .article .article-excerpt{position:relative;color:#aaa;font-size:16px;line-height:20px;overflow:hidden;height:80px
}
.a-list article:hover a.photo span{opacity:0.7
}
.a-list article:hover .article-title a{color:#62bbc3
}
@media screen and (max-width: 1200px){
.a-list article{width:45%;height:auto
}
.a-list article .article .article-excerpt{font-size:14px
}
}
@media screen and (max-width: 700px){
.a-list article{width:95%;height:auto
}
.a-list article .article .article-excerpt{font-size:15px
}
.a-list article .article .article-title a{font-size:18px
}
}



