
.s-item{display:inline-block;width:31%;height:auto;margin:1%;background-color:#fff;border:1px solid #eee;-webkit-box-shadow:0 1px 4px rgba(0,0,0,0.27),0 0 60px rgba(0,0,0,0.06) inset;box-shadow:0 1px 4px rgba(0,0,0,0.27),0 0 40px rgba(0,0,0,0.06) inset;position:relative;*zoom:1;overflow:hidden;cursor:pointer
}
.s-item>a{position:relative;display:inline-block;width:100%;height:100%
}
.s-item .left{float:left;width:30%;height:100px
}
.s-item .left img{width:100%;height:100%;vertical-align:middle
}
.s-item .right{float:left;width:65%;margin-left:2%
}
.s-item .right .title{font-size:16px;color:#62bbc3
}
.s-item .right p{font-size:13px;color:#aaa
}
@media screen and (max-width: 1400px){
.s-item{width:47%
}
}
@media screen and (max-width: 500px){
.s-item{width:100%;margin:5px;height:108px
}
.s-item .left{float:left;width:30%
}
.s-item .left img{width:100%;height:100%
}
.s-item .right{width:70%;height:100%;margin:0px
}
.s-item .right .title{margin-left:2%
}
.s-item .right p{margin-left:2%;width:95%
}
}



