
#slider-bar{width:250px;height:100%;position:fixed;left:0;top:0;background-color:#202020;overflow-y:auto;overflow-x:hidden;z-index:1;color:#999;opacity:1;-webkit-overflow-scrolling:touch;-webkit-transition:all 0.5s ease-out;transition:all 0.5s ease-out;-webkit-transform:translate3d(0, 0, 0);transform:translate3d(0, 0, 0);z-index:1000
}
#slider-bar::-webkit-scrollbar{width:0px;height:0px;background-color:#202020;-webkit-border-radius:4px;border-radius:4px
}
#slider-bar::-webkit-scrollbar-thumb{background-color:rgba(235,87,86,0.73);-webkit-border-radius:4px;border-radius:4px
}
#slider-bar .profile{padding-top:40px;padding-bottom:10px
}
#slider-bar .profile a{width:140px;display:block;margin:0 auto;-webkit-transition:-webkit-transform 6s ease-out;transition:-webkit-transform 6s ease-out;transition:transform 6s ease-out;transition:transform 6s ease-out, -webkit-transform 6s ease-out
}
#slider-bar .profile a img{width:140px;height:140px;-webkit-border-radius:70px;border-radius:70px;overflow:hidden
}
#slider-bar .profile a:hover{-webkit-transform:rotate3d(0, 0, 1, 360deg);transform:rotate3d(0, 0, 1, 360deg)
}
#slider-bar .profile span{display:block;padding:10px 0;font-size:20px;color:#999;text-align:center
}
#slider-bar .menus{margin:0;padding:0
}
#slider-bar .menus li{position:relative;display:block;width:100%;height:45px;line-height:45px;font-size:16px;overflow:hidden
}
#slider-bar .menus li a{padding-left:45px;height:100%;display:block;color:#999;-webkit-transition:color 0.2s cubic-bezier(0.4, 0.01, 0.165, 0.99);transition:color 0.2s cubic-bezier(0.4, 0.01, 0.165, 0.99);text-decoration:none
}
#slider-bar .menus li a i{display:inline-block;font-size:19px;width:25px;text-align:center;margin-right:18px
}
#slider-bar .menus li a span{display:inline-block;line-height:45px
}
#slider-bar .menus li a:hover{color:rgba(255,255,255,0.9)
}
#slider-bar .menus li a.active{color:rgba(255,255,255,0.9);background-color:rgba(0,0,0,0.2)
}
.ripple{display:block;position:absolute;background-color:rgba(255,255,255,0.2);-webkit-border-radius:100%;border-radius:100%;height:100px;width:100px;-webkit-transform:scale(0);-ms-transform:scale(0);transform:scale(0)
}
.ripple.animated{-webkit-animation:ripple 1s linear;animation:ripple 1s linear
}
@-webkit-keyframes ripple{
100%{-webkit-transform:scale(12);transform:scale(12);background-color:transparent
}
}
@keyframes ripple{
100%{-webkit-transform:scale(12);transform:scale(12);background-color:transparent
}
}
.navTop{display:none
}
@media screen and (max-width: 1000px){
#slider-bar{top:50px;-webkit-transform:translate3d(-250px, 0, 0);transform:translate3d(-250px, 0, 0)
}
.navTop{display:block;position:fixed;width:100%;height:50px;background-color:#202020;z-index:9999
}
.navTop p{color:white;text-align:center;line-height:50px;font-size:20px
}
.navTop p a{color:white;text-decoration:none
}
.navTop p i{cursor:pointer;position:absolute;display:inline-block;left:10px;font-size:25px
}
}
@media screen and (max-width: 500px){
#slider-bar{opacity:0.9
}
#slider-bar .slider-content{position:relative;width:100%;height:600px;overflow:auto
}
#slider-bar .profile{padding-top:20px
}
#slider-bar .profile a{width:80px
}
#slider-bar .profile a img{width:80px;height:80px
}
#slider-bar .profile span{display:none
}
#slider-bar .menus li{font-size:15px;height:40px;line-height:40px
}
#slider-bar .menus li a i{font-size:16px
}
}



