
.photo-list{margin:auto;margin-top:30px;width:80%;-webkit-column-count:4;-moz-column-count:4;column-count:4;-moz-column-gap:0px;-webkit-column-gap:0px;column-gap:0px
}
.photo-list .photo-item{float:left;display:inline-block;margin:0px  3px;-webkit-transition:all 1s ease-out;transition:all 1s ease-out
}
.photo-list .photo-item img{width:100%;cursor:-webkit-zoom-in;cursor:zoom-in
}
.error{text-align:center;color:red;font-size:20px
}
.l-show{position:fixed;width:100%;height:100%;padding-left:100px;top:0;left:0;background-color:rgba(0,0,0,0.8);z-index:100;cursor:-webkit-zoom-out;cursor:zoom-out;background-position:center center;background-size:cover
}
.l-show img{display:block;margin:auto;padding-top:10px;max-width:95%;height:80%
}
.t-show{position:fixed;width:100%;height:100%;padding-left:100px;top:0;left:0;z-index:200;cursor:-webkit-zoom-out;cursor:zoom-out;background-position:center center;background-repeat:no-repeat;background-size:cover
}
.t-show img{display:block;margin:auto;padding-top:10px;max-width:95%;height:80%
}
@media screen and (max-width: 1400px){
.photo-list{margin:auto;margin-top:15px;width:80%;-webkit-column-count:3;-moz-column-count:3;column-count:3;-moz-column-gap:0px;-webkit-column-gap:0px;column-gap:0px
}
}
@media screen and (max-width: 1000px){
.photo-list{-webkit-column-count:2;-moz-column-count:2;column-count:2
}
.l-show{padding:0;margin-top:50px
}
.t-show{padding:0;margin-top:50px
}
}
@media screen and (max-width: 500px){
.photo-list{width:100%;-webkit-column-count:1;-moz-column-count:1;column-count:1;margin:3px 0 0 0
}
.photo-list .photo-item{width:96%;margin:0 2%
}
.photo-list .photo-item img{width:100%;height:auto
}
.l-show img{width:95%;height:auto
}
.t-show img{width:95%;height:auto
}
}



