
span.clip{display:inline-block;width:100%;line-height:30px;text-overflow:ellipsis;overflow:hidden;white-space:nowrap
}
.music{position:fixed;width:100%;height:100%;font-size:14px;line-height:1.5;font-family:poppin,'PingFang SC',Tahoma,Arial,\5FAE\8F6F\96C5\9ED1,sans-serif;color:rgba(225,225,225,0.8)
}
.music .bg{position:absolute;top:0;left:0;width:100%;height:100%;background-repeat:no-repeat;background-size:cover;background-position:50%;-webkit-filter:blur(60px);filter:blur(60px);opacity:.8
}
.music .mask{position:absolute;top:0;left:0;width:100%;height:100%;background-color:#292a2b;background-color:rgba(0,0,0,0.35);z-index:2
}
.music .show-music{position:absolute;margin-left:5%;width:90%;padding:5%;color:#e1e1e1;z-index:3
}
.music .show-music .left{float:left;width:40%;height:425px;overflow-y:scroll;overflow-x:hidden
}
.music .show-music .left::-webkit-scrollbar{width:8px;height:8px;background-color:#202020;-webkit-border-radius:4px;border-radius:4px
}
.music .show-music .left::-webkit-scrollbar-thumb{background-color:rgba(235,87,86,0.73);-webkit-border-radius:4px;border-radius:4px
}
.music .show-music .left ul{display:block;width:100%;height:100%
}
.music .show-music .left ul li{line-height:42px;text-align:left;cursor:pointer
}
.music .show-music .left ul li span{display:inline-block;line-height:100%;text-overflow:ellipsis;overflow:hidden;white-space:nowrap
}
.music .show-music .left ul li span.sing-name{width:50%
}
.music .show-music .left ul li span.singer-name{width:20%
}
.music .show-music .left ul li span.sing-time{width:10%
}
.music .show-music .left ul span.control{width:10%;opacity:0;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none
}
.music .show-music .left ul span.control i{font-size:18px;line-height:100%;margin-right:5px
}
.music .show-music .left ul span.control:hover{opacity:1
}
.music .show-music .left ul .living{display:none;width:10px;height:10px;margin-right:10px;background-image:url(http://y.gtimg.cn/mediastyle/yqq/img/wave.gif?max_age=2592000&v=78979d47cc7dc55cab5d36b4c96168d5)
}
.music .show-music .left ul li.on span{color:white
}
.music .show-music .left ul li.on span.control{opacity:1
}
.music .show-music .left ul li.on .living{display:inline-block
}
.music .show-music .left ul li:hover span.control{opacity:1
}
.music .show-music .right{float:left;width:40%;height:425px;overflow-x:hidden;overflow-y:hidden
}
.music .show-music .right::-webkit-scrollbar{width:8px;height:8px;background-color:#202020;-webkit-border-radius:4px;border-radius:4px
}
.music .show-music .right::-webkit-scrollbar-thumb{background-color:rgba(235,87,86,0.73);-webkit-border-radius:4px;border-radius:4px
}
.music .show-music .right .l-box{position:relative;width:100%;height:auto;overflow:hidden
}
.music .show-music .right .l-box .show-img{position:absolute;width:240px;height:240px;left:50%;margin-left:-110px;-webkit-border-radius:100%;border-radius:100%;background-size:cover;background-image:url(http://music.163.com/style/mobile/img/share/disc-ip6.png)
}
.music .show-music .right .l-box .show-img img{position:absolute;top:45px;left:155px;width:62%;height:62%;-webkit-transition:-webkit-transform 1s ease;transition:-webkit-transform 1s ease;transition:transform 1s ease;transition:transform 1s ease, -webkit-transform 1s ease
}
.music .show-music .right .l-box .show-img img.animate-img{-webkit-animation:showImg 35s linear infinite;animation:showImg 35s linear infinite
}
@-webkit-keyframes showImg{
0%{-webkit-transform:rotate3d(0, 0, 0, 360deg);transform:rotate3d(0, 0, 0, 360deg)
}
100%{-webkit-transform:rotate3d(0, 0, 1, 360deg);transform:rotate3d(0, 0, 1, 360deg)
}
}
@keyframes showImg{
0%{-webkit-transform:rotate3d(0, 0, 0, 360deg);transform:rotate3d(0, 0, 0, 360deg)
}
100%{-webkit-transform:rotate3d(0, 0, 1, 360deg);transform:rotate3d(0, 0, 1, 360deg)
}
}
.music .show-music .right .l-box .show-lyr{position:relative;margin-top:250px;width:105%;height:200px;overflow-y:auto
}
.music .show-music .right .l-box .l-lyr{position:relative;width:100%;-webkit-transition:all 0.4s ease-out;transition:all 0.4s ease-out;margin-bottom:200px
}
.music .show-music .right .l-box .l-lyr p{text-align:center;line-height:35px;-webkit-transition:all 0.38s ease-out;transition:all 0.38s ease-out
}
.music .show-music .right .l-box .l-lyr p.on{color:#31c27c
}
.music .show-music .controls{position:fixed;width:50%;margin-left:10%;bottom:10px;color:#e1e1e1
}
.music .show-music .controls .control-label{float:left;width:200px;height:90px;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none
}
.music .show-music .controls .control-label i{font-size:40px;margin-right:10px;cursor:pointer
}
.music .show-music .controls .show-info{width:65%;margin-top:5px;float:left
}
.music .show-music .controls .show-info .i-l{float:left
}
.music .show-music .controls .show-info .i-r{float:right
}
.music .show-music .controls .progress{position:relative;width:65%;display:inline-block;margin-top:10px;padding-top:10px;height:20px;cursor:pointer
}
.music .show-music .controls .progress .line{position:relative;height:2px;background:rgba(255,255,255,0.2)
}
.music .show-music .controls .progress .lineTo{position:absolute;top:10px;left:0px;width:0%;height:2px;background:rgba(255,255,255,0);-webkit-transition:all 0.05s linear;transition:all 0.05s linear;z-index:2
}
.music .show-music .controls .progress .lineIn{position:absolute;top:10px;left:0px;width:0%;height:2px;background:rgba(255,255,255,0.7);-webkit-transition:all 0.05s linear;transition:all 0.05s linear;z-index:10
}
.music .show-music .controls .progress .lineIn i.icontdot,.music .show-music .controls .progress .lineIn i::before{position:absolute;font-size:28px;top:-20px;right:5px;width:10px;height:10px;background-position:0 -170px;opacity:1;-webkit-filter:none;filter:none;color:white
}
.music .show-music .controls .volume{float:right;position:relative;margin-top:-10px;width:120px
}
.music .show-music .controls .volume i{cursor:pointer;font-size:25px
}
.music .show-music .controls .volume .volume-line{position:absolute;width:85px;top:20px;left:30px;height:4px;background:rgba(255,255,255,0.2);z-index:1;cursor:pointer
}
.music .show-music .controls .volume .volume-to{position:absolute;top:20px;left:30px;height:4px;background:rgba(255,255,255,0.8);z-index:2;cursor:pointer
}
.music .show-music .controls .volume .volume-to i.icontdot,.music .show-music .controls .volume .volume-to i:before{position:absolute;top:-16px;right:-8px;z-index:3
}
.music .show-music .serach-music{margin-bottom:10px
}
.music .show-music .serach-music input{width:140px;line-height:31px;border:0;background:none;border-bottom:1px solid white;font-size:18px;outline:inherit;z-index:1;padding-left:5px;color:white;width:150px;-webkit-transition:width 1s ease-out;transition:width 1s ease-out
}
.music .show-music .serach-music select{display:inline-block;width:80px;height:33px;border:0;font-size:18px;outline:inherit;z-index:1;padding-left:5px
}
.music .show-music .serach-music button{display:inline-block;line-height:1;white-space:nowrap;cursor:pointer;background:#fff;border:1px solid #c0ccda;color:#1f2d3d;-webkit-appearance:none;text-align:center;-webkit-box-sizing:border-box;box-sizing:border-box;outline:none;margin:0;-moz-user-select:none;-webkit-user-select:none;-ms-user-select:none;padding:9px 15px;font-size:15px;-webkit-border-radius:4px;border-radius:4px;color:#fff;background-color:#50bfff;border-color:#50bfff
}
@media screen and (max-width: 1400px){
.music .show-music{margin-left:2%;padding:4%
}
.music .show-music .left{height:340px
}
.music .show-music .left ul li span.control{width:15%
}
.music .show-music .right{width:40%;height:340px
}
.music .show-music .right .l-box .show-img{width:180px;height:180px;margin-left:-90px
}
.music .show-music .right .l-box .show-img img{top:34px;left:125px
}
.music .show-music .right .l-box .show-lyr{margin-top:200px
}
.music .show-music .controls{margin-left:0;width:70%
}
}
@media screen and (max-width: 1000px){
.music .show-music .right{width:50%
}
}
@media screen and (max-width: 800px){
.music .show-music{width:100%;margin-left:0;padding-left:1%
}
.music .show-music .left{width:105%;height:202px
}
.music .show-music .right{width:100%;height:105px;line-height:35px;text-align:left
}
.music .show-music .right .l-box{height:105px
}
.music .show-music .right .l-box .show-img{display:none
}
.music .show-music .right .l-box .show-lyr{margin-top:10px;height:100px
}
.music .show-music .right .l-box .show-lyr p{opacity:0.6
}
.music .show-music .right .l-box .show-lyr p.on{opacity:1
}
.music .show-music .controls{width:100%;bottom:0px;font-size:12px
}
.music .show-music .controls .control-label{width:35%;height:60px;text-align:center
}
.music .show-music .controls .control-label i{font-size:30px;margin-right:2px;margin-left:0px
}
.music .show-music .controls .show-info{margin-top:-90px;margin-left:10px;width:80%
}
.music .show-music .controls .progress{width:50%;margin-top:15px;margin-left:0
}
.music .show-music .controls .progress .lineIn i{top:-18px
}
.music .show-music .controls .volume{display:none
}
}
@media screen and (max-width: 320px){
.music .show-music .left table td:first-child{max-width:190px
}
}



